/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import { useState, } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { useSnackbar } from 'notistack';
import {
  Card,
  Table,
  Button,
  Checkbox,
  TableRow,
  TableBody,
  TableCell,
  Container,
  TableContainer,
  Switch,
} from '@mui/material';
// swal 
import swal from 'sweetalert';
// format
import FormControlLabel from '@mui/material/FormControlLabel';
// routes
import { PATH_ADMIN } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Iconify from '../../components/Iconify';
import Scrollbar from '../../components/Scrollbar';

import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';

// sections
import { UserListHead,  UserMoreMenu } from '../../sections/admin/user/list';


// axios

import axios from '../../utils/axios'; 
import useAuth from '../../hooks/useAuth';

// ----------------------------------------------------------------------

const TABLE_HEAD = [
  { id: 'title', label: 'Title', alignRight: false },
  { id: 'description', label: 'Description', alignRight: false },
  { id: 'image_url', label: 'Image Url', alignRight: false },
  { id: 'link', label: 'Link', alignRight: false },
];
// ----------------------------------------------------------------------

export default function Feature() {
  const { themeStretch } = useSettings();
  const { setting } = useAuth()
  const [order, setOrder] = useState('asc');
  const [selected, setSelected] = useState([]);
  const [orderBy, setOrderBy] = useState('id');
  const [dense, Setdense] = useState(false);
  const { enqueueSnackbar } = useSnackbar();
  const AccessToken =  window.localStorage.getItem('accessToken');

  

  const handleRequestSort = (property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };
  const handleSelectAllClick = (checked) => {
    if (checked.target.checked) {
      const newSelecteds = setting?.feature.map((n) => n.id);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (id) => {
    const selectedIndex = selected.indexOf(id);
    let newSelected = [];
    if (selectedIndex === -1) {
        newSelected = newSelected.concat(selected, id);
      } else if (selectedIndex === 0) {
        newSelected = newSelected.concat(selected.slice(1));
      } else if (selectedIndex === selected.length - 1) {
        newSelected = newSelected.concat(selected.slice(0, -1));
      } else if (selectedIndex > 0) {
        newSelected = newSelected.concat(
          selected.slice(0, selectedIndex),
          selected.slice(selectedIndex + 1),
        );
      }
    setSelected(newSelected);
  };




  const handleDeleteUser = (userId) => {
    swal({
        title: "Note",
        text: `you about to delete`,
        icon: "info",
        buttons: true,
        dangerMode: true,
      })
      .then((willDelete) => {
        if (willDelete) {
            axios.post(`/api/delete/feature/${AccessToken}/system`,{
                feature_id:userId
            }).then(() => {
                enqueueSnackbar('deleted')

    setSelected([]);
  
            }).catch(error => {
                if(error.data?.message){
                    enqueueSnackbar(error.data.message, { variant: 'error' })   
                }else if(error?.message){
                    enqueueSnackbar(error.message, { variant: 'error' })   
                }else{
                    enqueueSnackbar('An Error Occured', { variant: 'error' })   
                }
               
            });
        } else {
            swal("Request Cancelled");
          }
        });
    
  };

 

 


  function handleChangeDense(event){
   Setdense(event.target.checked);
  }

  return (
    <Page title="User: Feature Image">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Feature Image"
          links={[
            { name: 'Dashboard', href: PATH_ADMIN.root },
            { name: 'List' },
          ]}
          action={
            <Button
              variant="contained"
              component={RouterLink}
              to={PATH_ADMIN.general.newfeature}
              startIcon={<Iconify icon={'eva:plus-fill'} />}
            >
              New Feature Image
            </Button>
          }
        />

        <Card>
      
       

          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table
              size={dense ? 'small' : 'medium'}
              >
                <UserListHead
                  order={order}
                  orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  rowCount={setting?.feature?.length}
                  numSelected={selected.length}
                  onRequestSort={handleRequestSort}
                  onSelectAllClick={handleSelectAllClick}
                  onDeleteUsers
                />

                <TableBody>
                  {setting?.feature.map((row) => {
                    const { id, title,  description, image, link } = row;
                    const isItemSelected = selected.indexOf(id) !== -1;
                    return (
                      <TableRow
                        hover
                        key={id}
                        tabIndex={-1}
                        role="checkbox"
                        selected={isItemSelected}
                        aria-checked={isItemSelected}
                      >
                        <TableCell>
                          <Checkbox checked={isItemSelected} onClick={() => handleClick(id)} />
                        </TableCell>

                        <TableCell align="left">{title}</TableCell>
                        <TableCell align="left">{description}</TableCell>
                        <TableCell align="left">{image}</TableCell>
                        <TableCell align="left">{link}</TableCell>
                       

                        <TableCell align="right">
                          <UserMoreMenu onDelete={() => handleDeleteUser(id)} userName={'no_edit'} />
                        </TableCell>
                      </TableRow>
                    );
                  })}
                 
                  
                </TableBody>
              </Table>
            </TableContainer>
          </Scrollbar>
          
          <FormControlLabel
        control={<Switch checked={dense} onChange={(event) => handleChangeDense(event)} />}
        label="Dense padding" align="left"
      />   
         
    
            
        </Card>
      </Container>
    </Page>
  );
}

// ----------------------------------------------------------------------

 


  
